@import url(https://fonts.googleapis.com/css?family=Orbitron);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
    margin: 0;
    width: 100%;
    height: 100vh;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Orbitron',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.abbr {
    text-decoration: none;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


.animationRight {
    -webkit-animation: moveRight 4s forwards;
            animation: moveRight 4s forwards;
    width: 120px;
    height: 120px;
    position: absolute;
    left: 10px;
}

.animationLeft {
    -webkit-animation: moveLeft 4s forwards;
            animation: moveLeft 4s forwards;
    width: 120px;
    height: 120px;
    position: absolute;
    right: 30px;
}

.background {
    width: 100%;
    height: 100%;
    background-color: transparent;
}

.animationFadeOut {
    -webkit-animation: fadeOut 2s forwards;
            animation: fadeOut 2s forwards;
    width: 100%;
    height: 100%;
}

.animationFadeIn {
    -webkit-animation: fadeIn 2.5s forwards;
            animation: fadeIn 2.5s forwards;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.animationBeBlack {
    background-color: black;
    width: 100%;
    height: 100%;
}

@-webkit-keyframes moveRight {
    0% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px)
    }
    100% {
        -webkit-transform: translateX(1340px);
                transform: translateX(1340px)
    }
}

@keyframes moveRight {
    0% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px)
    }
    100% {
        -webkit-transform: translateX(1340px);
                transform: translateX(1340px)
    }
}

@-webkit-keyframes moveLeft {
    0% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px)
    }
    100% {
        -webkit-transform: translateX(-1360px);
                transform: translateX(-1360px)
    }
}

@keyframes moveLeft {
    0% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px)
    }
    100% {
        -webkit-transform: translateX(-1360px);
                transform: translateX(-1360px)
    }
}

@-webkit-keyframes fadeIn {
    0% {
        background-color: black;
    }
    100% {
        background-color: transparent;
    }
}

@keyframes fadeIn {
    0% {
        background-color: black;
    }
    100% {
        background-color: transparent;
    }
}

@-webkit-keyframes fadeOut {
    0% {
        background-color: transparent;
    }
    100% {
        background-color: black;
    }
}

@keyframes fadeOut {
    0% {
        background-color: transparent;
    }
    100% {
        background-color: black;
    }
}
#menu {
    background: radial-gradient(circle at 45%, transparent, rgba(97, 114, 131, 0.15) 50%, rgba(97, 114, 131, 0.45) 75%, transparent 85%), linear-gradient(to right, transparent 35%, rgba(97, 114, 131, 0.25) 70%);
    position: absolute;
    width: calc(2*200px);
    height: calc(2*200px);
    margin: 0 auto;
    overflow: hidden;
    border-radius: calc(2*100px);
    -moz-border-radius: calc(2*100px);
    -webkit-border-radius: calc(2*100px);
    -webkit-animation: fade-in .5s;
            animation: fade-in .5s;
}

#center {
    position: absolute;
    left: calc(2*70px);
    top: calc(2*70px);
    width: calc(2*60px);
    height: calc(2*60px);
    z-index: 10;
    background: transparent;
    border-radius: calc(2*30px);
    -moz-border-radius: calc(2*30px);
    -webkit-border-radius: calc(2*30px);
}
#center div {
    display: block;
    width: 100%;
    height: 100%
}
.item {
    background: transparent;
    overflow: hidden;
    position: absolute;
    width: calc(2*100px);
    height: calc(2*100px);
    transform-origin: 100% 100%;
    -moz-transform-origin: 100% 100%;
    -webkit-transform-origin: 100% 100%;
    transition: background .5s;
    -moz-transition: background .5s;
    -webkit-transition: background .5s;
    -o-transition: background .5s;
    -ms-transition: background .5s;
}
.item div:first-child:first-child:hover {
    color: #821230;
}
.item1 {
    z-index: 1;
    transform: rotate(60deg);
    -moz-transform: rotate(60deg);
    -webkit-transform: rotate(60deg);
}
.item2 {
    z-index: 2;
    transform: rotate(120deg);
    -moz-transform: rotate(120deg);
    -webkit-transform: rotate(120deg);
}
.item3 {
    z-index: 3;
    transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
}
.item4 {
    z-index: 4;
    transform: rotate(240deg);
    -moz-transform: rotate(240deg);
    -webkit-transform: rotate(240deg);
}
.item5 {
    z-index: 5;
    transform: rotate(300deg);
    -moz-transform: rotate(300deg);
    -webkit-transform: rotate(300deg);
}
.item6 {
    border: none;
    position: absolute;
    z-index: 6;
    transform: rotate(-30deg);
    -moz-transform: rotate(-30deg);
    -webkit-transform: rotate(-30deg);
}
#wrapper6 {
    position: absolute;
    width: calc(2*100px);
    height: calc(2*100px);
    overflow: hidden;
    transform-origin: 100% 100%;
    -moz-transform-origin: 100% 100%;
    -webkit-transform-origin: 100% 100%;
}
.item1 .content {
    left: calc(2*-10px);
    top: calc(2*15px);
    transform: rotate(-60deg);
    -moz-transform: rotate(-60deg);
    -webkit-transform: rotate(-60deg);
}
.item2 .content {
    left: calc(2*-11px);
    top: calc(2*16px);
    transform: rotate(-120deg);
    -moz-transform: rotate(-120deg);
    -webkit-transform: rotate(-120deg);
}
.item3 .content {
    left: calc(2*-7px);
    top: calc(2*12px);
    transform: rotate(-180deg);
    -moz-transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
}
.item4 .content {
    left: calc(2*-5px);
    top: calc(2*18px);
    transform: rotate(-240deg);
    -moz-transform: rotate(-240deg);
    -webkit-transform: rotate(-240deg);
}
.item5 .content {
    left: calc(2*-10px);
    top: calc(2*20px);
    transform: rotate(-300deg);
    -moz-transform: rotate(-300deg);
    -webkit-transform: rotate(-300deg);
}
.item6 .content {
    left: calc(2*20px);
    top: calc(2*-10px);
    transform: rotate(30deg);
    -moz-transform: rotate(30deg);
    -webkit-transform: rotate(30deg);
}
.content, .content div {
    width: 100%;
    height: 100%;
    text-align: center
}
.content {
    position: absolute;
}
.content div {
    line-height: calc(2*100px);
    display: block;
    position: absolute;
    text-decoration: none;
    font-family: 'Segoe UI', Arial, Verdana, sans-serif;
    font-size: 20px;
    color: #ccc
}
.display-target {
    display: none;
    text-align: center;
    opacity: 0;
}
.display-target:target {
    display: block;
    opacity: 1;
    animation: fade-in 1s;
    -moz-animation: fade-in 1s;
    -webkit-animation: fade-in 1s;
    -o-animation: fade-in 1s;
    -ms-animation: fade-in 1s;
}
@keyframes fade-in {
    from { opacity: 0 }
    to { opacity: 1 }
}
@-webkit-keyframes fade-in {
    from { opacity: 0 }
    to { opacity: 1 }
}
.react-calendar {
    width: 100%;
    max-width: 100%;
    height: 100%;
    background: transparent;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.125em;
}

.react-calendar--doubleView {
    width: 700px;
}

.react-calendar--doubleView .react-calendar__viewContainer {
    display: flex;
    margin: -0.5em;
}

.react-calendar--doubleView .react-calendar__viewContainer > * {
    width: 50%;
    margin: 0.5em;
}

.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
    box-sizing: border-box;
}

.react-calendar button {
    margin: 0;
    border: 0;
    outline: none;
}

.react-calendar button:enabled:hover {
    cursor: pointer;
}

.react-calendar__navigation {
    height: 44px;
    margin-bottom: 1em;
}

.react-calendar__navigation button {
    min-width: 44px;
    background: none;
}

.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
    background-color: #656565;
}

.react-calendar__navigation button[disabled] {
    background-color: #f0f0f0;
}

.react-calendar__month-view__weekdays {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 0.9rem;
    color: #d9d9d9;
}

.react-calendar__month-view__weekdays__weekday {
    padding: 0.8rem;
}

.react-calendar__month-view__weekNumbers {
    font-weight: bold;
}

.react-calendar__month-view__weekNumbers .react-calendar__tile {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8em;
    padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
    color: #aeb4b5;
}

.react-calendar__month-view__days__day {
    color: #aeb4b5;
}

.react-calendar__month-view__days__day--weekend {
    color: rgb(130, 18, 48);
}

.react-calendar__month-view__days__day--neighboringMonth {
    color: rgb(134, 188, 194);
}

.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
    padding: 2em 0.5em;
}

.react-calendar__tile {
    max-width: 100%;
    text-align: center;
    padding: 0.75em 0.5em;
    background: none;
}

.react-calendar__tile:disabled {
    background-color: #f0f0f0;
}

.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
    background-color: #e6e6e6;
}

.react-calendar__tile--now {
    color: #FFFFFF;
    box-shadow: #474747 0px 0px 12px 2px inset;
    border-radius: 8px;
}

.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
    background: #ffffa9;
}

.react-calendar__tile--hasActive {
    background: #76baff;
}

.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
    background: #a9d4ff;
}

.react-calendar__tile--active {
    background: #006edc;
    color: white;
}

.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
    background: #1087ff;
}

.react-calendar--selectRange .react-calendar__tile--hover {
    background-color: #e6e6e6;
}

